import React, { useState, useEffect } from 'react'
import { View, Text, Image, ScrollView } from '@tarojs/components'
import Taro, { useDidShow, usePullDownRefresh } from '@tarojs/taro'
import { AtTabs, AtTabsPane, AtIcon } from 'taro-ui'
import { orderService, OrderData } from '@/services/order'
import './index.scss'

const OrderListPage: React.FC = () => {
  const [current, setCurrent] = useState(0)
  const [orders, setOrders] = useState<OrderData[]>([])
  const [loading, setLoading] = useState(true)
  const [hasMore, setHasMore] = useState(false)
  const [page, setPage] = useState(1)

  const tabList = [
    { title: 'ÂÖ®ÈÉ®' },
    { title: 'ÂæÖÊîØ‰ªò' },
    { title: 'ÂæÖÊúçÂä°' },
    { title: 'Â∑≤ÂÆåÊàê' }
  ]

  const statusMap = {
    0: undefined, // ÂÖ®ÈÉ®
    1: 'pending', // ÂæÖÊîØ‰ªò
    2: 'paid', // ÂæÖÊúçÂä°
    3: 'completed' // Â∑≤ÂÆåÊàê
  } as const

  useDidShow(() => {
    setPage(1)
    fetchOrders(1)
  })

  useEffect(() => {
    setPage(1)
    fetchOrders(1)
  }, [current])

  usePullDownRefresh(async () => {
    await fetchOrders(1)
    Taro.stopPullDownRefresh()
  })

  const fetchOrders = async (pageNum: number = page) => {
    try {
      setLoading(true)
      const status = statusMap[current]

      // Ëé∑ÂèñÊâÄÊúâËÆ¢ÂçïÔºàÊúçÂä°ËÆ¢Âçï + ‰∫ßÂìÅËÆ¢ÂçïÔºâÔºåÁÑ∂ÂêéÊ†πÊçÆdisplayStatusÁ≠õÈÄâ
      let orderList = await orderService.getOrderList(
        undefined, // ÂÖà‰∏çÊåâpaymentStatusÁ≠õÈÄâ
        undefined, // ‰∏çËøáÊª§ËÆ¢ÂçïÁ±ªÂûãÔºåÂêåÊó∂Ëé∑ÂèñÊúçÂä°ËÆ¢ÂçïÂíå‰∫ßÂìÅËÆ¢Âçï
        pageNum,
        100 // Ëé∑ÂèñÊõ¥Â§ö‰ª•‰æøÁ≠õÈÄâ
      )

      // Ê†πÊçÆÊ†áÁ≠æÈ°µÁ≠õÈÄâËÆ¢Âçï
      if (status) {
        orderList = orderList.filter(order => {
          const displayStatus = order.displayStatus || order.paymentStatus

          // ÁâπÊÆäÂ§ÑÁêÜÔºöÂ∑≤ÂÆåÊàêÊ†áÁ≠æÈ°µÊòæÁ§∫completedÁä∂ÊÄÅ
          if (status === 'completed') {
            return displayStatus === 'completed'
          }
          // ÂæÖÊúçÂä°Ê†áÁ≠æÈ°µÔºöÊòæÁ§∫paidÁä∂ÊÄÅÔºàÂåÖÊã¨pendingÂíåconfirmedÁöÑÈ¢ÑÁ∫¶Ôºâ
          if (status === 'paid') {
            return displayStatus === 'paid'
          }
          // ÂæÖÊîØ‰ªòÊ†áÁ≠æÈ°µ
          if (status === 'pending') {
            return displayStatus === 'pending'
          }

          return displayStatus === status
        })
      }

      // Âè™ÂèñÂâç20Êù°
      const paginatedList = orderList.slice(0, 20)

      if (pageNum === 1) {
        setOrders(paginatedList)
      } else {
        setOrders(prev => [...prev, ...paginatedList])
      }

      setPage(pageNum)
      setHasMore(paginatedList.length === 20)
    } catch (error) {
      console.error('Ëé∑ÂèñËÆ¢ÂçïÂ§±Ë¥•:', error)
      if (pageNum === 1) {
        setOrders([])
      }
    } finally {
      setLoading(false)
    }
  }

  const handleTabClick = (index: number) => {
    setCurrent(index)
  }

  const handleOrderClick = (orderNo: string) => {
    Taro.navigateTo({
      url: `/pages/order/detail/index?orderNo=${orderNo}`
    })
  }

  const handlePayOrder = async (e: any, order: OrderData) => {
    e.stopPropagation()

    // üöÄ ÊîπËøõÔºöÊï∞ÊçÆÈ™åËØÅ
    console.log('üí≥ ÂáÜÂ§áÊîØ‰ªòËÆ¢Âçï:', {
      orderNo: order.orderNo,
      amount: order.amount,
      amountType: typeof order.amount
    })

    // È™åËØÅÈáëÈ¢ùÊúâÊïàÊÄß
    if (!order.amount || typeof order.amount !== 'number' || isNaN(order.amount)) {
      Taro.showToast({
        title: 'ËÆ¢ÂçïÈáëÈ¢ùÊó†ÊïàÔºåÊó†Ê≥ïÊîØ‰ªò',
        icon: 'none'
      })
      console.error('‚ùå ÊîØ‰ªòÈ™åËØÅÂ§±Ë¥•ÔºöÊó†ÊïàÁöÑËÆ¢ÂçïÈáëÈ¢ù', { orderNo: order.orderNo, amount: order.amount })
      return
    }

    try {
      // Ëé∑ÂèñÊîØ‰ªòÂèÇÊï∞
      const paymentParams = await orderService.getPaymentParams(order.orderNo)

      // Ë∞ÉÁî®ÂæÆ‰ø°ÊîØ‰ªò
      Taro.requestPayment({
        ...paymentParams,
        success: async () => {
          // Êõ¥Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
          await orderService.updateOrderStatus(order.orderNo, 'paid')
          Taro.showToast({
            title: 'ÊîØ‰ªòÊàêÂäü',
            icon: 'success'
          })
          // Âà∑Êñ∞ËÆ¢ÂçïÂàóË°®
          fetchOrders()
        },
        fail: (err) => {
          if (err.errMsg !== 'requestPayment:fail cancel') {
            Taro.showToast({
              title: 'ÊîØ‰ªòÂ§±Ë¥•',
              icon: 'none'
            })
          }
        }
      })
    } catch (error) {
      console.error('‚ùå Ëé∑ÂèñÊîØ‰ªòÂèÇÊï∞Â§±Ë¥•:', error)
      Taro.showToast({
        title: 'Ëé∑ÂèñÊîØ‰ªòÂèÇÊï∞Â§±Ë¥•',
        icon: 'none'
      })
    }
  }

  const handleCancelOrder = async (e: any, order: OrderData) => {
    e.stopPropagation()

    Taro.showModal({
      title: 'ÂèñÊ∂àËÆ¢Âçï',
      content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àËØ•ËÆ¢ÂçïÂêóÔºü',
      success: async (res) => {
        if (res.confirm) {
          try {
            const result = await orderService.cancelOrder(order.orderNo)

            if (result.refundAmount && result.refundAmount > 0) {
              Taro.showToast({
                title: `Â∑≤ÂèñÊ∂àÔºåÈÄÄÊ¨æÔø•${(result.refundAmount / 100).toFixed(2)}`,
                icon: 'success',
                duration: 2500
              })
            } else {
              Taro.showToast({
                title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
                icon: 'success'
              })
            }

            // Âà∑Êñ∞ÂàóË°®
            fetchOrders(1)
          } catch (error: any) {
            Taro.showToast({
              title: error.message || 'ÂèñÊ∂àÂ§±Ë¥•',
              icon: 'none'
            })
          }
        }
      }
    })
  }

  const handleRebookOrder = (e: any, order: OrderData) => {
    e.stopPropagation()
    // Ë∑≥ËΩ¨Âà∞È¢ÑÁ∫¶È¶ñÈ°µÈáçÊñ∞È¢ÑÁ∫¶
    Taro.switchTab({
      url: '/pages/appointment/index'
    })
  }

  const getStatusText = (order: OrderData) => {
    // ‰ΩøÁî®ÁªºÂêàÊòæÁ§∫Áä∂ÊÄÅÔºà‰ºòÂÖàÔºâÊàñÊîØ‰ªòÁä∂ÊÄÅÔºàÈôçÁ∫ßÔºâ
    const status = order.displayStatus || order.paymentStatus

    const statusTextMap = {
      'pending': 'ÂæÖÊîØ‰ªò',
      'paid': 'ÂæÖÊúçÂä°',
      'serving': 'ÊúçÂä°‰∏≠',
      'completed': 'Â∑≤ÂÆåÊàê',  // üöÄ ÁÆ°ÁêÜÂëòÊ†áËÆ∞ÁöÑÂÆåÊàêÁä∂ÊÄÅ
      'cancelled': 'Â∑≤ÂèñÊ∂à',
      'refunded': 'Â∑≤ÈÄÄÊ¨æ'
    }
    return statusTextMap[status] || status
  }

  const getStatusClass = (order: OrderData) => {
    // ‰ΩøÁî®ÁªºÂêàÊòæÁ§∫Áä∂ÊÄÅÔºà‰ºòÂÖàÔºâÊàñÊîØ‰ªòÁä∂ÊÄÅÔºàÈôçÁ∫ßÔºâ
    const status = order.displayStatus || order.paymentStatus

    const statusClassMap = {
      'pending': 'status-pending',
      'paid': 'status-paid',
      'serving': 'status-serving',
      'completed': 'status-completed',  // üöÄ Â∑≤ÂÆåÊàêÊ†∑Âºè
      'cancelled': 'status-cancelled',
      'refunded': 'status-refunded'
    }
    return statusClassMap[status] || ''
  }

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr)
    const month = date.getMonth() + 1
    const day = date.getDate()
    const hour = date.getHours()
    const minute = date.getMinutes()
    return `${month}Êúà${day}Êó• ${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
  }

  const renderOrderItem = (order: OrderData) => (
    <View
      key={order.orderNo}
      className="order-item"
      onClick={() => handleOrderClick(order.orderNo)}
    >
      <View className="order-header">
        {/* ÊúçÂä°ËÆ¢ÂçïÊòæÁ§∫Èó®Â∫óÂêç */}
        {order.orderType === 'service' && (
          <Text className="store-name">{order.storeName}</Text>
        )}
        {/* ‰∫ßÂìÅËÆ¢ÂçïÊòæÁ§∫‰∫ßÂìÅÂêçÁß∞ */}
        {order.orderType === 'product' && (
          <Text className="store-name">{order.title}</Text>
        )}
        <Text className={`order-status ${getStatusClass(order)}`}>
          {getStatusText(order)}
        </Text>
      </View>

      <View className="order-content">
        {/* ÊúçÂä°ËÆ¢ÂçïÊòæÁ§∫ÊäÄÂ∏àÂ§¥ÂÉè */}
        {order.orderType === 'service' && order.therapistAvatar && (
          <Image
            className="therapist-avatar"
            src={order.therapistAvatar}
          />
        )}
        {/* ‰∫ßÂìÅËÆ¢ÂçïÊòæÁ§∫ÈªòËÆ§‰∫ßÂìÅÂõæÁâá */}
        {order.orderType === 'product' && (
          <Image
            className="product-image"
            src="https://mingyitang1024.com/static/default.png"
          />
        )}

        <View className={`order-info ${!order.therapistAvatar && order.orderType === 'service' ? 'no-image' : ''}`}>
          {/* üöÄ ÊúçÂä°ËÆ¢ÂçïÊ≤°ÊúâÂ§¥ÂÉèÊó∂ÊòæÁ§∫Âç†‰ΩçÁ¨¶ */}
          {/* ÊúçÂä°ËÆ¢Âçï‰ø°ÊÅØ */}
          {order.orderType === 'service' && (
            <>
              <View className="info-row">
                <Text className="therapist-name">{order.therapistName}</Text>
                <Text className="service-name">{order.serviceName}</Text>
              </View>
              <View className="info-row">
                <Text className="appointment-time">
                  È¢ÑÁ∫¶Êó∂Èó¥Ôºö{formatDate(`${order.appointmentDate} ${order.startTime}`)}  {/* ‚úÖ Êîπ‰∏∫ startTime */}
                </Text>
              </View>
            </>
          )}

          {/* ‰∫ßÂìÅËÆ¢Âçï‰ø°ÊÅØ */}
          {order.orderType === 'product' && (
            <>
              <View className="info-row">
                <Text className="service-name">{order.title}</Text>
                {order.extraData?.quantity && (
                  <Text className="quantity">√ó{order.extraData.quantity}</Text>
                )}
              </View>
              <View className="info-row">
                <Text className="appointment-time">
                  Ë¥≠‰π∞Êó∂Èó¥Ôºö{formatDate(order.createdAt)}
                </Text>
              </View>
            </>
          )}

          <View className="info-row">
            <Text className="order-no">ËÆ¢ÂçïÂè∑Ôºö{order.orderNo}</Text>
          </View>
        </View>
      </View>

      <View className="order-footer">
        <View className="price-info">
          <Text className="label">ÂÆû‰ªòÔºö</Text>
          <Text className="price">¬•{(order.totalAmount / 100).toFixed(2)}</Text>
        </View>
        <View className="action-buttons">
          {/* ÂæÖÊîØ‰ªòËÆ¢ÂçïÔºöÊòæÁ§∫ÊîØ‰ªòÂíåÂèñÊ∂à */}
          {(order.displayStatus || order.paymentStatus) === 'pending' && (
            <>
              <View className="button cancel" onClick={(e) => handleCancelOrder(e, order)}>
                ÂèñÊ∂àËÆ¢Âçï
              </View>
              <View className="button pay" onClick={(e) => handlePayOrder(e, order)}>
                ÂéªÊîØ‰ªò
              </View>
            </>
          )}

          {/* ÂæÖÊúçÂä°ËÆ¢ÂçïÔºö‰ªÖÊúçÂä°ËÆ¢ÂçïÊòæÁ§∫ÂèñÊ∂à */}
          {(order.displayStatus || order.paymentStatus) === 'paid' && order.orderType === 'service' && (
            <View className="button cancel" onClick={(e) => handleCancelOrder(e, order)}>
              ÂèñÊ∂àËÆ¢Âçï
            </View>
          )}

          {/* ÊúçÂä°‰∏≠ËÆ¢ÂçïÔºö‰∏çÊòæÁ§∫Êìç‰ΩúÊåâÈíÆ */}
          {(order.displayStatus || order.paymentStatus) === 'serving' && null}

          {/* Â∑≤ÂÆåÊàêËÆ¢ÂçïÔºö‰ªÖÊúçÂä°ËÆ¢ÂçïÊòæÁ§∫ÂÜçÊ¨°È¢ÑÁ∫¶ */}
          {(order.displayStatus === 'completed') && order.orderType === 'service' && (
            <View className="button rebook" onClick={(e) => handleRebookOrder(e, order)}>
              ÂÜçÊ¨°È¢ÑÁ∫¶
            </View>
          )}

          {/* Â∑≤ÂèñÊ∂à/Â∑≤ÈÄÄÊ¨æËÆ¢ÂçïÔºö‰ªÖÊúçÂä°ËÆ¢ÂçïÊòæÁ§∫ÂÜçÊ¨°È¢ÑÁ∫¶ */}
          {(['cancelled', 'refunded'].includes(order.displayStatus || order.paymentStatus)) && order.orderType === 'service' && (
            <View className="button rebook" onClick={(e) => handleRebookOrder(e, order)}>
              ÂÜçÊ¨°È¢ÑÁ∫¶
            </View>
          )}
        </View>
      </View>
    </View>
  )

  const renderEmpty = () => (
    <View className="empty-state">
      <AtIcon value="file-generic" size="60" color="#ccc" />
      <Text className="empty-text">ÊöÇÊó†ËÆ¢Âçï</Text>
    </View>
  )

  const renderLoading = () => (
    <View className="loading-state">
      <Text>Âä†ËΩΩ‰∏≠...</Text>
    </View>
  )

  return (
    <View className="order-list-page">
      <AtTabs
        current={current}
        tabList={tabList}
        onClick={handleTabClick}
        className="order-tabs"
      >
        {tabList.map((tab, index) => (
          <AtTabsPane key={index} current={current} index={index}>
            <ScrollView scrollY className="order-list">
              {loading ? renderLoading() : (
                orders.length > 0 ? orders.map(renderOrderItem) : renderEmpty()
              )}
            </ScrollView>
          </AtTabsPane>
        ))}
      </AtTabs>
    </View>
  )
}

export default OrderListPage